import { Component, OnInit } from '@angular/core';
import * as lineIconList from '../../assets/icons/icon-list.json';
import * as icons8List from '../../assets/icons/icons8/categories.json';

export interface ILineIcons {
    iconList: string;
}

export interface IIcons8 {
    name: string;
    link: string;
    icons: Array<string>[];
}

@Component({
    selector: 'app-icons',
    templateUrl: './icons.component.html',
    styleUrls: ['./icons.component.scss'],
})
export class IconsComponent implements OnInit {
    lineIcons: ILineIcons = (lineIconList as any).default;
    icons8: IIcons8[] = (icons8List as any).default;

    visibleCategory: object;
    visibleCategoryName: string;

    hostname = window.location.host;

    constructor() {}

    ngOnInit(): void {
        this.visibleCategory = {};
        this.icons8.forEach((icon) => {
            this.visibleCategory[icon.name] = false;
        });
        this.visibleCategory[Object.keys(this.visibleCategory)[0]] = true;
        this.visibleCategoryName = Object.keys(this.visibleCategory)[0];
    }

    showCategory(category: string) {
        return this.visibleCategory[category];
    }

    toggleCategory(category: string) {
        if (this.visibleCategory[category]) {
            return;
        }
        this.visibleCategory[category] = true;
        this.visibleCategory[this.visibleCategoryName] = false;
        this.visibleCategoryName = category;
    }
}
